  //         <div class="menu__item-price">
  //           <div class="menu__item-cost">:</div>
  //           <div class="menu__item-total"><span>${price * transfer}</span> /</div>
  //         </div>
  //         `;

  //         document.querySelector('.menu .container').append(element);
  //       });
